import React from "react"

import ExternalLink from "./ExternalLink"
import HeadlineImage from "./HeadlineImage"
import Image from "./Image"

function Headline(props) {
  const data = props.data
  const headlineClass = data.headlineClass + " carousel-item"

  function hasLink(link) {
    return link && link.url
  }

  const linkClass =
    hasLink(data.headline.link) && data.headline.link.class
      ? data.headline.link.class
      : "btn btn-lg btn-primary"
  const linkRole =
    hasLink(data.headline.link) && data.headline.link.role
      ? data.headline.link.role
      : "button"

  return (
    <div className={headlineClass}>
      {data.image ? <Image src={data.image} alt={data.altImage} /> : null}
      {!data.image && data.imageText ? <HeadlineImage icon1={data.imageText} /> : null}
      {data.headline ? (
        <div className="container">
          <div className="carousel-caption text-left">
            <h1>{data.headline.title}</h1>
            <p
              className="card-text"
              dangerouslySetInnerHTML={{ __html: data.headline.description }}
            ></p>
            {data.headline.link && data.headline.link.url ? (
              <p>
                <ExternalLink
                  class={linkClass}
                  url={data.headline.link.url}
                  text={data.headline.link.name}
                  role={linkRole}
                />
              </p>
            ) : null}
          </div>
        </div>
      ) : null}
    </div>
  )
}

export default Headline
