import React from "react"

import Headline from "./Headline"

function HeadlineList(props) {
  const headlineClass = props.class + " carousel-inner"
  const headlines = []

  if (props.list && props.list.length > 0) {
    for (let i = 0; i < props.list.length; i++) {
      const headline = props.list[i]
      headlines.push(<Headline data={headline} key={headline.id} />)
    }
  }

  return <div className={headlineClass}>{headlines}</div>
}

export default HeadlineList
