import React from "react"

import HeadlineList from "./elements/HeadlineList"

import headlinesData from "../data/headlines.json"

function Headlines(props) {
  const headlines = headlinesData

  const indicators = []

  const isHeadlineActive = function (headlineClasses) {
    let active = false
    if (headlineClasses && headlineClasses.length > 0) {
      for (let j = 0; j < headlineClasses.length; j++) {
        let headlineClass = headlineClasses[j]
        active = "active" === headlineClass
      }
    }
    return active
  }

  if (headlines && headlines.length > 0) {
    for (let i = 0; i < headlines.length; i++) {
      const headline = headlines[i]
      indicators.push(
        <li
          data-target="#page-image"
          data-slide-to={i}
          // className={headline.headlineClass.includes("active") ? "active" : null}
          className={isHeadlineActive(headline.headlineClass) ? "active" : null}
          key={headline.id}
        ></li>
      )
    }
  }

  return (
    <div className="page-section" id="Headines">
      <div id="page-image" className="carousel slide" data-ride="carousel">
        {/* <!-- Indicators --> */}
        <ul className="carousel-indicators">{indicators}</ul>

        {/* <!-- The slideshow --> */}
        <HeadlineList list={headlines} />

        {/* <!-- Left and right controls --> */}
        <a className="carousel-control-prev" href="#page-image" data-slide="prev">
          <span className="carousel-control-prev-icon"></span>
        </a>
        <a className="carousel-control-next" href="#page-image" data-slide="next">
          <span className="carousel-control-next-icon"></span>
        </a>
      </div>
    </div>
  )
}

export default Headlines
