function Image(props) {
    const imageURL = (props && props.src && props.src.startsWith("/static")) ? (process.env.REACT_APP_ROUTER_BASE || "") + props.src : props.src;
    
    return (
        (<img className={props.className || ""} src={imageURL} alt={props.alt || ""} />)
    )
}


export default Image
