import React from "react"

function Speciality(props) {
  const data = props.data
  const languageList = []
  const frameworkList = []

  function setSepeciality(specialityArr, specialities) {
    if (specialities && specialities.length > 0) {
      for (let i = 0; i < specialities.length; i++) {
        specialityArr.push(<li key={specialities[i]}>{specialities[i]}</li>)
      }
    }
  }

  setSepeciality(languageList, data.languages)
  setSepeciality(frameworkList, data.frameworks)

  return (
    <div className="mt-2">
      <p>
        <u>specialities:</u>
      </p>
      <div className="row mb-3">
        <div className="col-6">
          <h6>Programming Languages</h6>
          <ul className="speciality">{languageList}</ul>
        </div>
        <div className="col-6">
          <h6>Frameworks</h6>
          <ul className="speciality">{frameworkList}</ul>
        </div>
      </div>
    </div>
  )
}

export default Speciality
