import React, { Component } from "react"
import "./App.css"

import Header from "./components/Header"
import PageContent from "./components/PageContent"
import Footer from "./components/Footer"

class App extends Component {
  componentDidMount() {
    window.onscroll = function () {
      let elements = document.querySelectorAll(".page-section")
      for (let i = 0; i < elements.length; i++) {
        let element = elements[i]
        let position = element.getBoundingClientRect()

        if (
          position.top < window.innerHeight / 3 &&
          position.bottom > window.innerHeight / 2
        ) {
          let currentLinkId = element.getAttribute("id")
          let currentLink = document.querySelector(
            'a[href="#' + currentLinkId + '"][class*="nav-link"'
          )
          if (currentLink && currentLink.parentElement) {
            let activeLink = document.querySelector(
              'li[class*="active"][class*="nav-item"]'
            )
            if (activeLink) {
              if (
                activeLink.childNodes[0] &&
                activeLink.childNodes[0].id === currentLinkId
              ) {
                return
              }
              let activeLinkClass = activeLink.getAttribute("class")
              activeLinkClass = activeLinkClass.replace("active", "")
              activeLink.setAttribute("class", activeLinkClass)
            }
            let currentLinkClasses = currentLink.parentElement.getAttribute("class")
            currentLinkClasses += " active"
            currentLink.parentElement.setAttribute("class", currentLinkClasses)
          }
        }
      }
    }

    let cardTextElements = document.querySelectorAll(".card-text")
    for (let i = 0; i < cardTextElements.length; i++) {
      let cardTextElement = cardTextElements[i]
      let textContent = cardTextElement.innerHTML
      let textContent1, textContent2
      if (textContent.length > 500) {
        textContent1 = textContent.substring(0, 499)
        textContent2 = textContent.substring(499, textContent.length)
        textContent =
          textContent1 +
          '<span id="dots">...</span><span id="more" style="display:none;">' +
          textContent2 +
          "</span>"
        textContent +=
          '<a onclick="readMore.clicked(this)" id="readMoreButton" class="read-more-button">Read more &gt;&gt;&gt;</a>'
        cardTextElement.textContent = null
        cardTextElement.innerHTML = textContent
      }
    }

    function collapseNavbar(e, type) {
      let navbarButton = document.getElementById("navbar-button")
      let isExpanded = navbarButton.getAttribute("aria-expanded") === "true"
      if (isExpanded) {
        navbarButton.click()
      }
    }

    let nonHeadEles = document.querySelectorAll(".non-head")
    for (let i = 0; i < nonHeadEles.length; i++) {
      let nonHeadEle = nonHeadEles[i]
      nonHeadEle.addEventListener("click", (e) => collapseNavbar(e, "click"))
    }
    // document.addEventListener("scroll", (e) => collapseNavbar(e, "scroll"))
  }
  render() {
    return (
      <div className="App container-fluid page-container">
        <Header />

        <PageContent />

        <Footer />
      </div>
    )
  }
}

export default App
