import React from "react"

import MyProtfolioBrand from "./MyProtfolioBrand"

import pageData from "../data/pages.json"

function Navbar(props) {
  const navigation = pageData

  const navlinks = []

  for (let i = 0; i < navigation.length; i++) {
    navlinks.push(
      <li className="nav-item" key={navigation[i].name}>
        <a className="nav-link" href={navigation[i].link}>
          {navigation[i].name}
        </a>
      </li>
    )
  }

  return (
    <nav className="navbar fixed-top navbar-expand-lg navbar-dark page-navbar" id="nav">
      <MyProtfolioBrand class="navbar-brand" />

      <button
        className="navbar-toggler"
        type="button"
        data-toggle="collapse"
        data-target="#navbarNav"
        aria-controls="navbarNav"
        aria-expanded="false"
        aria-label="Toggle navigation"
        id="navbar-button"
      >
        <span className="navbar-toggler-icon"></span>
      </button>

      <div className="collapse navbar-collapse" id="navbarNav">
        <ul className="navbar-nav">{navlinks}</ul>
      </div>
    </nav>
  )
}

export default Navbar
