import React from "react"

function ExternalLink(props) {
  return (
    <a
      className={props.class}
      href={props.url}
      target="_blank"
      rel="noopener noreferrer"
      role={props.role}
    >
      {props.text ? props.text : <i className={props.icon}></i>}
    </a>
  )
}

export default ExternalLink
