import React from "react"

import Article from "./Article"

function ArticleList(props) {
  const listClass = props.listClass + " card-flex"
  const articlelist = []

  if (props.list && props.list.length > 0) {
    for (let i = 0; i < props.list.length; i++) {
      const article = props.list[i]
      articlelist.push(<Article data={article} key={article.id} />)
    }
  }

  return <div className={listClass}>{articlelist}</div>
}

export default ArticleList
