import React from "react"

import Brand from "./elements/Brand"

import myProtfolioData from "../data/my-protfolio.json"

function MyProtfolioBrand(props) {
  const myProtfolio = myProtfolioData

  return (
    <Brand
      class={props.class}
      url={myProtfolio.url}
      title={myProtfolio.name}
      subtitle={myProtfolio.subtitle}
    />
  )
}

export default MyProtfolioBrand
