import React from "react"

import CardInfoList from "./elements/CardInfoList"

import experienceData from "../data/experience.json"
import Image from "./elements/Image"

function Experience(props) {
  const experience = experienceData

  return (
    <div className="row">
      <div className="col-4 parallax-exp">
        <Image src="/static/images/skill-0.png" alt="Skills" />
      </div>
      <CardInfoList class="col-8" list={experience} />
    </div>
  )
}

export default Experience
