import React from "react"

function Article(props) {
  const data = props.data

  const articleClass = data.articleClass + " card-flex-div"
  const icons = []
  
  const titleImageURL = (data && data.image && data.image.startsWith("/static")) ? (process.env.REACT_APP_ROUTER_BASE || "") + data.image : data.image;

  function hasIcons(item) {
    return item && item.icons && item.icons.length > 0
  }

  if (hasIcons(data)) {
    for (let i = 0; i < data.icons.length; i++) {
      icons.push(<i className={data.icons[i]} key={data.icons[i]}></i>)
    }
  }

  return (
    <div className={articleClass}>
      {data.image ? (
        <div
          className="dev-title-image"
          style={titleImageURL ? { backgroundImage: `url(${titleImageURL})` } : null}
        >
          {data.imageText ? (
            <p className="dev-title-image-text">
              <span dangerouslySetInnerHTML={{ __html: data.imageText.symbol }}></span>
              <br />
              {data.imageText.title}
            </p>
          ) : null}
        </div>
      ) : data.imageText ? (
        <div className="dev-title-image">
          <p className="dev-title-image-text">
            <span dangerouslySetInnerHTML={{ __html: data.imageText.symbol }}></span>
            <br />
            {data.imageText.title}
          </p>
        </div>
      ) : null}

      {data.quote ? (
        <div
          className="mb-4 text-center blockquote"
          dangerouslySetInnerHTML={{ __html: data.quote }}
        ></div>
      ) : null}
      <p
        className="dev-text card-text"
        dangerouslySetInnerHTML={{ __html: data.description }}
      ></p>
      {hasIcons(data) ? <div className="dev-icons">{icons}</div> : null}
    </div>
  )
}

export default Article
