import React from "react"
import Image from "./Image"

function PageSection(props) {
  const data = props.data
  const sectionClass = props.class ? "page-section " + props.class : "page-section"
  const pageContent = props.content

  function hasParallax(parallax) {
    return parallax && parallax.class
  }

  const parallaxClass = hasParallax(data.parallax) ? data.parallax.class : null
  const parallaxContentClass = hasParallax(data.parallax)
    ? data.parallax.contentClass
    : null
  const containerClass = "container " + parallaxContentClass
  const titleImageClass = data.titleImage
    ? "page-icon " + data.titleImageClass
    : "page-icon"

  return (
    <div className={sectionClass} id={data.name}>
      {parallaxClass ? <div className={parallaxClass}></div> : null}
      <div className={containerClass}>
        <div className="row">
          <div className="col-lg-12">
            {data.titleImage ? (
              <Image
                className={titleImageClass}
                src={data.titleImage}
                alt={data.altTitleImage}
              />
            ) : null}
            <fieldset>
              <legend>
                <h2 dangerouslySetInnerHTML={{ __html: data.title }}></h2>
              </legend>
              {pageContent()}
            </fieldset>
          </div>
        </div>
      </div>
    </div>
  )
}

export default PageSection
