import React from "react"

import PageSection from "./elements/PageSection"
import PageSections from "./PageSections"

import pagesData from "../data/pages.json"

function PageContent(props) {
  const pages = pagesData
  const pageSectionList = []

  if (pages && pages.length > 0) {
    for (let i = 0; i < pages.length; i++) {
      const page = pages[i]
      const content = PageSections.get(page.name)
      if (content) {
        pageSectionList.push(<PageSection data={page} content={content} key={page.id} />)
      }
    }
  }

  return <div className="page-content non-head">{pageSectionList}</div>
}

export default PageContent
