import React from "react"

import ExternalLink from "./ExternalLink"
import Image from "./Image"

function CardInfo(props) {
  const data = props.data
  const heading1 = []
  const heading2 = []

  function setHeading(headingsArr, headings) {
    for (let i = 0; i < headings.length; i++) {
      const heading = headings[i]
      headingsArr.push(
        <div key={heading.id}>
          {heading.url ? (
            <ExternalLink class="org-link" url={heading.url} text={heading.title} />
          ) : (
            heading.title
          )}
          {heading.logo ? (
            <Image className="org-logo" src={heading.logo} alt={heading.altLogo} />
          ) : null}
          <address>{heading.address}</address>
        </div>
      )
    }
  }

  setHeading(heading1, data.heading1)
  setHeading(heading2, data.heading2)

  return (
    <div className="card-info">
      <div className="row">
        <div className="col-9 text-left">
          <h5 className="card-title">{heading1}</h5>
          <h6 className="card-subtitle">{heading2}</h6>
          <p>{data.faculty}</p>
          <p>{data.grade}</p>
        </div>
        <div className="col-3 text-right">
          <p>{data.timeInterval}</p>
        </div>
      </div>
      <p className="card-text" dangerouslySetInnerHTML={{ __html: data.description }}></p>
    </div>
  )
}

export default CardInfo
