import React from "react"

function DevIconList(props) {
  const icons = props.data

  const iconList = []

  for (let i = 0; i < icons.length; i++) {
    iconList.push(
      <li className="list-inline-item" key={icons[i]}>
        <i className={icons[i]}></i>
      </li>
    )
  }

  return <ul className="list-inline dev-icons">{iconList}</ul>
}

export default DevIconList
