import React from "react"

import MyProtfolioBrand from "./MyProtfolioBrand"
import SocialLinks from "./SocialLinks"

function Footer(props) {
  return (
    <footer className="page-footer non-head">
      <div className="sides"></div>
      <div className="mid">
        <MyProtfolioBrand />
        <SocialLinks />
      </div>
      <div className="sides">
        <a className="go-to-top" href="/#">
          <i className="fa fa-arrow-up mr-1"></i>
          Go To Top
        </a>
      </div>
    </footer>
  )
}

export default Footer
